import { useState, useEffect, createContext, useContext } from 'react';
import { User } from '../types';
import { apiService } from '../services/api';

interface AuthContextType {
  user: User | null;
  login: (email: string, password: string) => Promise<void>;
  register: (userData: any) => Promise<void>;
  logout: () => void;
  isLoading: boolean;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};

// Real authentication with backend integration
export const useAuthProvider = () => {
  const [user, setUser] = useState<User | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    // Check for stored user session and validate token
    const checkStoredAuth = async () => {
      const storedUser = localStorage.getItem('ifad_user');
      const storedToken = localStorage.getItem('ifad_token');
      
      if (storedUser && storedToken) {
        try {
          // Validate token with backend and get fresh user data
          const response = await apiService.getCurrentUser();
          if (response.success) {
            setUser(response.data);
          } else {
            // Clear invalid session
            localStorage.removeItem('ifad_user');
            localStorage.removeItem('ifad_token');
          }
        } catch (error) {
          console.error('Session validation failed:', error);
          localStorage.removeItem('ifad_user');
          localStorage.removeItem('ifad_token');
        }
      }
      setIsLoading(false);
    };
    
    checkStoredAuth();
  }, []);

  const login = async (email: string, password: string) => {
    setIsLoading(true);
    
    try {
      // Call real backend authentication API
      const response = await apiService.login(email, password);
      
      if (response.success) {
        const { token, user: userData } = response.data;
        
        // Store token and user data
        localStorage.setItem('ifad_token', token);
        localStorage.setItem('ifad_user', JSON.stringify(userData));
        
        setUser(userData);
      } else {
        throw new Error(response.message || 'Login failed');
      }
    } catch (error) {
      console.error('Login error:', error);
      throw error;
    } finally {
      setIsLoading(false);
    }
  };

  const register = async (userData: any) => {
    setIsLoading(true);
    try {
      // Call real backend registration API
      const response = await apiService.register(userData);
      
      if (response.success) {
        // Registration successful - for our local backend, registration doesn't auto-login
        // User will need to login separately
        
        // For hosts, trigger admin notification
        if (userData.role === 'host' || !userData.role) {
          try {
            await triggerAdminNotification('new_host', response.data, userData);
          } catch (notificationError) {
            console.warn('Admin notification failed:', notificationError);
            // Don't fail registration if notification fails
          }
        }
      } else {
        throw new Error(response.message || 'Registration failed');
      }
    } catch (error) {
      console.error('Registration error:', error);
      throw error;
    } finally {
      setIsLoading(false);
    }
  };

  const logout = async () => {
    try {
      // Call backend logout if needed
      await apiService.logout();
    } catch (error) {
      console.error('Logout error:', error);
    } finally {
      // Clear local storage regardless of API call success
      localStorage.removeItem('ifad_user');
      localStorage.removeItem('ifad_token');
      setUser(null);
    }
  };

  return {
    user,
    login,
    register,
    logout,
    isLoading,
  };
};

// Admin notification function
const triggerAdminNotification = async (type: 'new_host' | 'host_verified' | 'urgent_review', user: User, additionalData?: any) => {
  try {
    // In production, this would send real notifications (email, push, etc.)
    console.log(`Admin notification triggered: ${type}`, { user, additionalData });
    
    // Submit host registration to API service for tracking
    if (type === 'new_host' && additionalData) {
      await apiService.submitHostRegistration(additionalData);
    }
    
    // Mock notification service calls
    const notificationData = {
      type,
      timestamp: new Date().toISOString(),
      user,
      message: getNotificationMessage(type, user),
      priority: type === 'urgent_review' ? 'high' : 'medium',
      channels: ['email', 'dashboard'], // Multiple notification channels
    };
    
    // In real implementation, would call notification service
    console.log('Notification sent to admins:', notificationData);
    
    // Store notification in browser storage for demo
    const existingNotifications = JSON.parse(localStorage.getItem('admin_notifications') || '[]');
    existingNotifications.unshift(notificationData);
    // Keep only last 50 notifications
    if (existingNotifications.length > 50) {
      existingNotifications.splice(50);
    }
    localStorage.setItem('admin_notifications', JSON.stringify(existingNotifications));
    
    return { success: true };
  } catch (error) {
    console.error('Failed to send admin notification:', error);
    return { success: false, error };
  }
};

const getNotificationMessage = (type: string, user: User): string => {
  switch (type) {
    case 'new_host':
      return `New host registration: ${user.firstName} ${user.lastName} (${user.email}) requires review and verification.`;
    case 'host_verified':
      return `Host ${user.firstName} ${user.lastName} has been verified and is now active.`;
    case 'urgent_review':
      return `Urgent: Host ${user.firstName} ${user.lastName} requires immediate admin attention.`;
    default:
      return `Admin notification for user ${user.firstName} ${user.lastName}`;
  }
};

export { AuthContext };